<template>
  <div class="page">
    <AnimateHeader
      bg-color="#F5F5F5 "
      menu-bg-color="#F0F0F0"
      color="#3A3A3A"
      fill="#3A3A3A"
      stroke="#3A3A3A"
      title="账号冻结申诉"
      :on-back-button-click="back"
      :expanded="false"
      :animate="false"
    >
    </AnimateHeader>
    <div class="page-content unfreeze-account">
      <div class="normal" v-if="account_status == 1">您的账号正常，无需解冻</div>
      <div class="result" v-if="account_status == 2">申诉已提交，请耐心等待</div>
      <div class="freeze" v-if="account_status == 3">
        <div class="title">您的账号已被冻结</div>
        <div class="freeze-action">
          <div class="remind">以下任意行为都将导致账号冻结：</div>
          <p>1、 越狱</p>
          <p>2、 刷机</p>
          <p>3、 使用苹果共享帐号</p>
          <p style="margin-bottom: 22px;">4、 其他等违规获取收益的情况</p>
          <p>如您认为自己在使用钱咖的过程中并没有以上的恶意行为，请申诉解冻</p>
        </div>
        <button class="button button-submit" @click="submit()">申诉解冻</button>
      </div>

    </div>
  </div>
</template>
<style lang='scss' scoped>
  @import "../../../scss/mixins";

  .unfreeze-account {
    padding-top: 50px;
    background: #FFFFFF;
    padding-left: 20px;

    .normal {
      text-align: center;
      margin-top: 160px;
      margin-left: -10px;
      font-size: 24px;
      height: 33px;
      line-height: 33px;
      color: #b5b5b5;

      @include font-regular();
    }

    .result {
      text-align: center;
      margin-left: -10px;
      margin-top: 160px;
      height: 33px;
      line-height: 33px;
      font-size: 24px;
      color: #3a3a3a;

      @include font-regular();
    }

    .freeze {
      .title {
        margin-top: 60px;
        margin-left: -10px;
        margin-bottom: 50px;
        text-align: center;
        height: 33px;
        font-size: 24px;
        line-height: 33px;
        color: #3a3a3a;

        @include font-regular();
      }

      .freeze-action {
        width: 335px;
        height: 190px;
        margin-bottom: 40px;

        .remind {
          margin-bottom: 12px;
          height: 22px;
          line-height: 22px;
          font-size: 16px;
          color: #3a3a3a;
        }

        @include font-regular();

        p {
          font-size: 13px;
          line-height: 22px;
          height: 22px;
          color: #888888;
          margin-bottom: 0;

          @include font-regular();
        }
      }

      .button-submit {
        display: block;
        margin: 0 auto;
        width: 172px;
        height: 40px;
        min-height: 40px;
        line-height: 40px;
        background: #ef502b;
        color: #FFFFFF;
        font-size: 13px;
        border-radius: 2px;

        @include font-semibold()
      }
    }
  }
</style>
<script>
  import AnimateHeader from '@/widget/animate-header/Index'
  import {mapState, mapActions} from 'vuex'

  export default {
    components: {
      AnimateHeader
    },

    mounted () {
      this.getAccountStatus()
    },

    computed: {
      ...mapState('help', [
        'account_status'
      ])
    },

    methods: {
      ...mapActions('help', [
        'getAccountStatus'
      ]),

      submit () {
        $router.forward('/help/UnfreezeForm')
      },

      back () {
        $router.back('/help/self')
      }
    }
  }
</script>



